html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 150rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.header {
  background-color: red;
  display: flex;
}
.header img {
  height: 8rem;
  width: 8rem;
  margin: 0.5rem;
  padding: 0.5rem;
  border-radius: 4rem;
}
.header img:hover {
  background-color: rgba(128, 128, 128, 0.5);
  cursor: pointer;
}
.header #boton_menu {
  height: 5rem;
  width: 5rem;
  margin: 2rem;
  border-radius: 1rem;
}
.header #boton_menu:hover {
  background-color: gray;
  cursor: pointer;
}
.header h1 {
  margin: auto;
}

.footer {
  position: absolute;
  bottom: 0;
  background: red;
  width: 100%;
}

.navegador {
  position: absolute;
  right: 0;
  top: 0;
  width: 0;
  height: 100vh;
  background: red;
  transition: all 0.5s ease;
  overflow: hidden;
}
.navegador .nav_list {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: flex-start;
  margin-top: 90px;
  padding: 0;
  visibility: hidden;
  opacity: 0;
}
.navegador .nav_list li {
  list-style-type: none;
  width: 100%;
  margin: 0;
  padding: 1rem;
}
.navegador .nav_list li a {
  display: block;
  color: white;
  text-shadow: 1px 1px black;
}
.navegador .nav_list li:hover {
  background: rebeccapurple;
}
.navegador.show {
  display: block;
  width: 15rem;
}
.navegador.show .nav_list {
  visibility: visible;
  opacity: 1;
}

#boton_menu {
  z-index: 100;
}

.main {
  width: 100%;
  left: 0;
  transition: all 0.5s ease;
}
.main.show {
  width: calc(100% - 15rem);
}

.btn-remito {
  background: #00a2ff;
  color: white;
  font-size: 17px;
  font-weight: 700;
}
.btn-remito.hover {
  color: rebeccapurple;
}
.btn-remito.selected {
  background: blue;
}

.contenido-remito {
  background-color: rgba(128, 128, 128, 0.1);
  padding: 2rem;
  border: 2px solid black;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  z-index: 1;
}
.contenido-remito .content {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.contenido-remito .content p {
  margin: 0;
}
.contenido-remito .content p:not(:first-child) {
  margin-left: 10rem;
}
.contenido-remito .content:not(:last-child)::after {
  content: " ";
  background-color: rgba(128, 128, 128, 0.05);
  width: 100%;
  height: 0.5rem;
  border-radius: 0.5rem;
  display: block;
  margin: 2rem;
}

.hidden {
  display: none;
}